body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.ticker {
  color: blue;
  height: 35px;
  -webkit-transition: height 3s;
  -o-transition: height 3s;
  transition: height 3s
}

.tickerHidden {
  color: red;
  height: 0px;
  -webkit-transition: height 3s;
  -o-transition: height 3s;
  transition: height 3s
}


